  thrshold2(): number[] {
    const lstBins: number[] = [];
    for (let i = 10; i <= 100; i += 10) {
      lstBins.push(i);
    }
    return lstBins;
  };

  thrshold1(): number[] {
    const lstThresholds: number[] = [];
    for (let i = 0.1; i < 1; i += 0.1) {
      lstThresholds.push(parseFloat(i.toFixed(2)));
    }
    return lstThresholds;
  };

