import { Component, Input, OnInit } from '@angular/core';
import {FormControl} from '@angular/forms';
import { DatacollectionService } from '../Datacollection-service.service'
import { EChartsOption } from 'echarts';
interface Pokemon {
  value: string;
  viewValue: string;
}

interface PokemonGroup {
  disabled?: boolean;
  name: string;
  pokemon: Pokemon[];
}

@Component({
  selector: 'app-Intersection',
  templateUrl: './Intersection.component.html',
  styleUrls: ['./Intersection.component.scss']
})

export class IntersectionComponent implements OnInit {
  @Input() item = true;
  overallEvalDropDown:[] = [];
  overallEvalData:[] = [];
  public BiasData: EChartsOption = null;
  public table: any = [];


  graph_line_chart: EChartsOption = {
    tooltip: {
      trigger: 'axis'
    },
    toolbox: {
      show: !1
    },
    xAxis: [
      {
        type: 'category',
        data: ['2000', '2001', '2002', '2003', '2004', '2005'],
        axisLabel: {
          fontSize: 10,
          color: '#9aa0ac'
        }
      }
    ],
    yAxis: [
      {
        type: 'value',
        axisLabel: {
          fontSize: 10,
          color: '#9aa0ac'
        }
      }
    ],
    series: [
      {
        name: 'sales',
        type: 'bar',
        data: [0.668],

        markLine: {
          data: [
            {
              type: 'average'
            }
          ]
        }
      }
    ],
    color: ['#9f78ff', '#32cafe']
  };
  pokemonControl = new FormControl('');
  pokemonGroups: PokemonGroup[] = [
    {
      name: 'Grass',
      pokemon: [
        {value: 'bulbasaur-0', viewValue: 'Bulbasaur'},
        {value: 'oddish-1', viewValue: 'Oddish'},
        {value: 'bellsprout-2', viewValue: 'Bellsprout'},
      ],
    },
    {
      name: 'Water',
      pokemon: [
        {value: 'squirtle-3', viewValue: 'Squirtle'},
        {value: 'psyduck-4', viewValue: 'Psyduck'},
        {value: 'horsea-5', viewValue: 'Horsea'},
      ],
    },
    {
      name: 'Fire',
      disabled: true,
      pokemon: [
        {value: 'charmander-6', viewValue: 'Charmander'},
        {value: 'vulpix-7', viewValue: 'Vulpix'},
        {value: 'flareon-8', viewValue: 'Flareon'},
      ],
    },
    {
      name: 'Psychic',
      pokemon: [
        {value: 'mew-9', viewValue: 'Mew'},
        {value: 'mewtwo-10', viewValue: 'Mewtwo'},
      ],
    },
  ];

  public sampleData = [
    31, 40, 28, 44, 60, 55, 68, 51, 42, 85, 77, 31, 40, 28, 44, 60, 55
  ];
  option3:any=null;
  _selectedModel:any = null;
  breadscrums = [
    {
      title: 'Target Drift',
      items: ['MlOps'],
      active: 'Target Drift'
    }
  ];

  constructor(private DatacollectionService:DatacollectionService) {}

  ngOnInit() {
    this.getDropDown();
    
  };

  getData(){
    this.DatacollectionService.getTargetDrift()
    .subscribe(data=>{
      if(data?.line_plot){
          this.BiasData = data?.line_plot;
          this.table = data?.metrics_table;
          console.log(this.option3,this.BiasData[this.option3?.key],"this.BiasData[this.option3]",this.BiasData)
          this._selectedModel = this.BiasData[this.option3]
      }
    })
  };

private getDropDown(){
  this.getData()
  this.DatacollectionService.getModelList()
  .subscribe(data=>{
      if(data){
        console.log(data)
        this.overallEvalDropDown = data;
        this.option3 = data[0]['key'];
        this.getData()
      }
  })
}

public onChange(data:string){
  console.log(data,"onchange",this.BiasData[data])
  this.option3 = data;
  this._selectedModel = this.BiasData[data]
}

public GetModel(){

};

addGraph(item:any){

  this.graph_line_chart['xAxis'] = item?.xAxis;
  this.graph_line_chart['yAxis'] = item?.yAxis;

  this.graph_line_chart['series'] = item?.xAxis;
  // console.log(this.graph_line_chart,"graph")
  return this.graph_line_chart;
}

overallEval(){
  
}
}
