import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainComponent } from './main/main.component';
import { mainDashboardRes} from './mainDashboardRes/mainDashboardRes.component';
import { AlertComponent } from './alerts/alerts.component';
import { Dashboard2Component } from './dashboard2/dashboard2.component';
import { Dashboard3Component } from './dashboard3/dashboard3.component';
import { IntersectionComponent } from './Intersection/Intersection.component';
import { MlOps } from './MlOps/MlOps.component';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'mainRes',
    pathMatch: 'full'
  },

  {
    path: 'MlOps',
    component: MlOps
  },

  {
    path: 'outlayer',
    component: mainDashboardRes
  },

  {
    path: 'dataDrift',
    component: MainComponent
  },
  {
    path: 'targetDrift',
    component: IntersectionComponent
  },
  {
    path: 'alert',
    component: AlertComponent
  },
  // {
  //   path: 'dashboard3',
  //   component: Dashboard3Component
  // }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MlopsRoutingModule {}
