import { Component, OnInit } from "@angular/core";
import { EChartsOption } from "echarts";
import { DatacollectionService } from "../Datacollection-service.service";

@Component({
  selector: "app-main",
  templateUrl: "./main.component.html",
  styleUrls: ["./main.component.scss"],
})
export class MainComponent implements OnInit {
  // Echarts bar graph line chart config ///////////////

  graph_line_chart: EChartsOption = {
    tooltip: {
      trigger: "axis",
    },
    toolbox: {
      show: !1,
    },
    xAxis: [
      {
        type: "category",
        data: ["2000", "2001", "2002", "2003", "2004", "2005"],
        axisLabel: {
          fontSize: 10,
          color: "#9aa0ac",
        },
      },
    ],
    yAxis: [
      {
        type: "value",
        axisLabel: {
          fontSize: 10,
          color: "#9aa0ac",
        },
      },
    ],
    series: [
      {
        name: "sales",
        type: "bar",
        data: [0.668],
        markLine: {
          data: [
            {
              type: "average",
            },
          ],
        },
      },
    ],
    color: ["#9f78ff", "#32cafe"],
  };
  // breadscrum //////////////////////////////////
  breadscrums = [
    {
      title: "Data Drift",
      items: ["MlOps"],
      active: "Data Drift",
    },
  ];


  dataList: any;
  dataList2: any = null;
  option1: any = "null";
  option2: any = "null";
  option3: any = "null";
  option4: any = "null";

  threshold1: any;
  threshold2: any;

  dataDriftResult: EChartsOption = null;
  dataDriftResult2: EChartsOption = null;

  dataDriftTable: any;
  constructor(private DatacollectionService: DatacollectionService) {}

  ngOnInit() {
    this.getData();
    this.getData2();
    this.getData3();
  }
  // options dropdown
  private getData() {
    this.DatacollectionService.dropDown1().subscribe((data) => {
      if (data) {
        console.log(data, "asa");
        if (data?.feat_list) {
          this.dataList = data?.feat_list;
        }
      }
    });
  }
  // thresholds dropdown
  private getData2() {
    this.threshold1 = this.DatacollectionService.thrshold1();
  }
  // Bin dropdown
  private getData3() {
    this.threshold2 = this.DatacollectionService.thrshold2();
  }
  // Drift data
  submitDrift() {
    console.log(this.option1, this.option2, this.option3, this.option4);
    this.DatacollectionService.getDataDriftResult(
      this.option1,
      this.option2,
      this.option3,
      this.option4
    ).subscribe((data) => {
      if (data) {
        if (data?.ref_hist) {
          console.log(data, "data?.ref_hist");
          // chart1
          this.dataDriftResult = data?.ref_hist;
          // chart2
          this.dataDriftResult2 = data?.inc_hist;
          this.dataDriftTable = data?.test_table;
        }
      }
    });
  }

  public onChange2(data: any) {
    // console.log(data,"option")
    this.option1 = data;
    this.DatacollectionService.dropDown2Feature(data).subscribe((data) => {
      if (data) {
        if (data?.length) {
          console.log(data, "data?.feat_list");

          this.dataList2 = [...data];
        }
      }
    });
  }
}
